}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  border-radius: 4px;
  flex-basis: calc(25% - 7.5px);
  padding: 0;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
